ooter\" class=\"dialog-footer\">\n                    <el-button @click=\"close\">Cancel</el-button>\n                </span>\n            </el-dialog>\n        </div>\n\n    </withLabel>\n</template>\n\n<script>\nimport withLabel from './withLabel.vue';\n\nexport default {\n    name: 'customMask',\n    props: ['listItem', 'value'],\n    components: {\n        withLabel\n    },\n    watch: {\n        model() {\n            this.$emit('input', this.model);\n        }\n    },\n    data() {\n        return {\n            model: this.value,\n            dialogVisible: false,\n            exampleInputs: [\n                {\n                    title: 'Date',\n                    mask: '00/00/0000',\n                    validInput: '10/21/2011'\n                },\n                {\n                    title: 'Social Security Number',\n                    mask: '000-00-0000',\n                    validInput: '987-65-4329'\n                },\n                {\n                    title: 'Course Code',\n                    mask: 'AAA 999',\n                    validInput: 'BIO 101'\n                },\n                {\n                    title: 'License Key',\n                    mask: '*-*-***',\n                    validInput: 'a9a-f0c-28Q'\n                },\n                // {\n                //     title: 'Zip Code w/ Optional Plus Four',\n                //     mask: '99999?-9999',\n                //     validInput: '23462 or 23462-4062'\n                // }\n            ]\n        }\n    },\n    methods: {\n        close() {\n            this.dialogVisible = false;\n        }\n    }\n}\n</script>\n\n<style>\n    mark {\n        background-color:#929292 !important;\n        color: #fff !important;\n        font-size: 11px;\n        padding: 5px;\n        display: inline-block;\n        line-height: 1;\n        \n    }\n</style>\n"],"sourceRoot":""}]);

